  content: "";
  height: var(--size-checked);
  width: var(--size-checked);
  border-radius: var(--border-radius);
  background-color: var(--background-checked);
  display: inline-block;
  vertical-align: middle;
  position: absolute;
  top: var(--top);
  left: var(--left-after);
  transform: translate(0, -50%);
